/* Starter stuff */
*{
	margin: 0;
	padding: 0;

	-webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
	-moz-box-sizing: border-box;    /* Firefox, other Gecko */
	box-sizing: border-box;         /* Opera/IE 8+ */
}

/* Variables */
:root {
	--bgcolor: #000;
	--fgcolor: #fff;
	--accent: #0ff;

	--black: #000;
	--white: #fff;
	--offWhite: #ddd;
	--gray0: #101010;
	--gray1: #181818;
	--gray2: #444;

	--header-fs: 1;
	--time-1: 100ms;
	--time-2: 250ms;
	--time-3: 500ms;
}
/* Media queries to set rem */
@media (min-width: 800px) {:root {font-size: 20px;}}
@media (max-width: 800px) {:root {font-size: 16px;}}
@media (max-width: 600px) {:root {font-size: 12px;}}

body {
	color: var(--fgcolor);
	background-color: var(--bgcolor);
}

/* Nav bar stuff */
header {
	font-size: calc(1rem * var(--header-fs));

	position: fixed;
	width: 100%;
	display: flex;
	justify-content: space-between;
	align-items: center;

	padding-left: 1em;
	height: 4em;

	background-color: var(--gray1);
	transition: var(--time-2) ease-in-out;
	z-index: 10;
}

.logo {
	height: 75%;
	transition: var(--time-3) ease;
}
.logo:hover {
	transform: rotateZ(360deg);
	transition: var(--time-3) ease;
}

#nav-link-list {
	list-style: none;
	display: flex;
}

#nav-link-list li {
	position: relative;
	margin: 0 1.3em;

	font-size: 1.5em;

	transition: var(--time-2) ease-in;

	cursor: pointer;
}
#nav-link-list li:hover {
	color: var(--accent);
}
#nav-link-list li::after {
	content: '';
	position: absolute;

	bottom: 0;
	left: 0;
	width: 100%;
	height: 2px;

	transform: scaleX(0);
	transition: transform var(--time-1) ease-in;

	background: var(--accent);
}
#nav-link-list li:hover::after {
	transform: scaleX(1);
}

/* Center Pane Stuff */
#center-pane1 {
	display: flex;
	justify-content: center;

	padding-left: 2rem;
	padding-right: 2rem;
	padding-bottom: 4rem;
	padding-top: calc(4*1rem*var(--header-fs));

	width: 100%;

	/* transition: var(--time-2) ease-in; */
}
#center-pane2 {
	max-width: 75ch;
}

/* Homepage stuff */
.heading {
	position: sticky;
	/* This hides part of the heading under the navbar to ensure that the stuff in the back is occluded */
	top: calc(4*1rem*var(--header-fs) - 1rem);
	padding-top: 0.5rem;
	padding-bottom: 0rem;

	transition: var(--time-2) ease-in;

	background-color: var(--bgcolor);
	z-index: 2;
}
h1 {
	margin-top: 1rem;
	padding-left: 1rem;
	color: var(--offWhite);
}
hr {
	/* margin-left: 1rem; */
	/* margin-right: 1rem; */
	margin-bottom: 1rem;
	border: 2px solid var(--offWhite);
}
.ext-link {
	position: relative;
	color: var(--accent);
	text-decoration: none;
	cursor: pointer;
}
.ext-link::after {
	content: '';
	position: absolute;

	bottom: 0;
	left: 0;
	width: 100%;
	height: 1px;

	transform: scaleX(0);
	transition: transform var(--time-1) ease-in;

	background: var(--accent);
}
.ext-link:hover::after {
	transform: scaleX(1);
}

button {
	font-size: 1rem;
	font-weight: bold;

	border: 0;
	cursor: pointer;

	/* width: 11rem; */
	width: 100%;
	padding: 0.5em;
	margin-top: 1rem;
	float: left;

	color: var(--bgcolor);
	border: 2px solid var(--accent);
	border-radius: 0.5em;
	background-color: var(--accent);
}
.button span {
	cursor: pointer;
	display: inline-block;
	position: relative;
	transition: var(--time-2);
}
.button span:after {
	content: '\00bb';
	position: absolute;
	opacity: 0;
	top: 0;
	right: -1rem;
	transition: var(--time-2);
}
.button:hover span {
	padding-right: 1.25rem;
}
.button:hover span:after {
	opacity: 1;
	right: 0;
}

.info-box {
	padding: 1rem;
	margin-bottom: 1rem;

	border-radius: 1rem;
	border-style: solid;
	border-width: 1px;
	
	border-color: var(--gray2);
	color: var(--fgcolor);
	background-color: var(--gray1);
}
#connection-list {
	padding-top: 1rem;
	display: flex;
}
.connection {
	position: relative;
	width: 4rem;
	margin-right: 2rem;
}
.connection-pic {
	height: 4rem;

	object-fit: cover;

	border-radius: 50%;

	transition: var(--time-3) ease;
}
.connection-pic:hover {
	transform: rotateZ(360deg);
	transition: var(--time-3) ease;
}
.connection-label {
	font-size: 1rem;
	font-weight: normal;
	text-align: center;

	position: relative;
	width: 120%;
	left: 50%;
	transform: translateX(-50%);
}

#project-box {
	display: flex;
}
.project-img-box {
	position: relative;
	align-self: flex-start;
	margin-top: 1rem;
}
.project-img-box img {

	max-width: 100%;
	height: auto;

	background-color: #555;
}
@media (max-width: 600px) {
	#project-box {flex-direction: column;}
}
@media (min-width: 600px) {
	.project-info {
		width: 50%;
		padding-right: 1rem;
	}
	.project-img-box {width: 50%}
}